import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { MenuService, MenuItem } from '../menu/menu.service';
import { AuthenticationService } from '../users/authentication.service';
import { GeneralService } from '../../general.service';
import { AuthService, SocialUser } from "angularx-social-login";
import {  GoogleLoginProvider } from "angularx-social-login";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  private user: SocialUser;
  private loggedIn: boolean;

  constructor(private general:GeneralService, private authService: AuthService, private auth:AuthenticationService, private router:Router, private menuService:MenuService) {
    this.router.events.subscribe(val => {
      if (val instanceof NavigationEnd) {
        let route = val.urlAfterRedirects;
        this.menuService.selectItem(route);
      }
    })  
   }

  ngOnInit() {
    this.authService.authState.subscribe((user) => {
      this.user = user;
      this.loggedIn = (user != null);
    });

    function errorAuth2(updateObject:AuthenticationService, error, router, menuService:MenuService, general:GeneralService) {
      console.log('errorAuth2');
      console.log(error);
      general.kill();
      menuService.kill();
      general.showLogin = true;
      router.navigate(['']); 
    }    
    function updateAuth2(updateObject:AuthenticationService, data, router, menuService:MenuService, general:GeneralService) {
      console.log('updateAuth2');
      if (data) {
        updateObject.picture = data.PICTURE;
        updateObject.username = data.DISPLAY_NAME;
        updateObject.usertype = data.USER_TYPE;
        updateObject.repid = data.REP_ID;
        updateObject.token = localStorage.getItem('ng2-ui-auth_token');
        updateObject.AuthHeaders.headers.Authorization += updateObject.token;
        general.initialize();
        menuService.initialize();
        menuService.showMenu = true; 
        console.log(menuService.initialScreen);
        router.navigate([menuService.initialScreen]);     
      }              
    }
    if (localStorage.getItem('ng2-ui-auth_token')) {
      this.menuService.hasInitialized = true;
      this.auth.token = localStorage.getItem('ng2-ui-auth_token');
      this.general.getData('/api/v2/me', null, updateAuth2, this.auth, this.menuService, this.general, errorAuth2);
    } 
  }

  signInWithGoogle(): void {
    this.authService.signIn(GoogleLoginProvider.PROVIDER_ID).then((user:SocialUser) => {
      //alert("Sign In Promise processing...");
      console.log('Called service from login component');
      this.general.showLogin = false;
      this.authService.authState.subscribe((user) => {
        //alert("Have the user...");
        this.user = user;
        this.loggedIn = (user != null);
        localStorage.setItem('ng2-ui-auth_token', user.idToken);
        this.auth.token = user.idToken;
        this.auth.AuthHeaders.headers.Authorization += this.auth.token;
        function errorAuth(updateObject:AuthenticationService, error, router, menuService:MenuService, general:GeneralService) {
          console.log('errorAuth');
          //alert("Error Auth");
          console.log(error);
          general.kill();
          menuService.kill();
          general.showLogin = true;
          router.navigate(['']); 
        }
        function updateAuth(updateObject:AuthenticationService, data, router, menuService:MenuService, general:GeneralService) {
          console.log('updateAuth');
          if (data) {
            //alert("Updating autho info");
            general.showLogin = false;
            updateObject.isAuthenticated = true;            
            updateObject.picture = data.PICTURE;
            updateObject.username = data.DISPLAY_NAME;
            updateObject.usertype = data.USER_TYPE;
            updateObject.repid = data.REP_ID;
            updateObject.token = localStorage.getItem('ng2-ui-auth_token');
            updateObject.AuthHeaders.headers.Authorization += updateObject.token;
            general.initialize();
            menuService.initialize();
            menuService.showMenu = true;
            console.log(menuService.initialScreen);
            router.navigate([menuService.initialScreen]);    
              
          }
          else {
            alert("Unable to Log you in. Your credentials are invalid");
            general.kill();
            menuService.kill();
            general.showLogin = true;
            router.navigate(['']);             
          }              
        }
        console.log('get user data');
        if (localStorage.getItem('ng2-ui-auth_token')) {
          //alert("getting user data");
          this.menuService.hasInitialized = true;
          this.general.getData('/api/v2/me', null, updateAuth, this.auth, this.menuService, this.general, errorAuth);  
        }
        //console.log('redirect');
        //this.router.navigate([this.menuService.initialScreen]);  
            
      }, (error) => {
        console.log(error);
        localStorage.removeItem('ng2-ui-auth_token');
        this.general.kill();
        this.menuService.kill();
        this.general.showLogin = true;
        this.router.navigate(['']);
        alert("Unable to Log you in. Google was unable to authenticate you");
      }, () => {

      } 
      
      );
    }).catch(() => {
            localStorage.removeItem('ng2-ui-auth_token');
            alert("Unable to Log you in. Your credentials are invalid");
            this.general.kill();
            this.menuService.kill();
            this.general.showLogin = true;
            this.router.navigate(['']); 

    });

  }

  signOut(): void {
    this.authService.signOut();
  }



}



